.auth__logo-container {
  background: #FFF;
  padding: 20px;
  width: 100%; }

.auth__logo {
  max-width: 500px;
  margin: 0 auto;
  display: block; }

.auth__button {
  text-decoration: none;
  text-align: center;
  border-radius: 5px;
  background: #458FD9;
  color: #FFF;
  padding: 20px;
  display: block;
  max-width: 300px;
  margin: 20px auto 0; }



